<template>
  <div class="wrapper" :class="roleId === 4 || roleId === 5 ? 'bg-other' : 'bg-white'">
    <div v-if="roleId === 4 || roleId === 5 " style="padding: 20px;width: 100%">
      <h2 style="margin-bottom: 20px">{{roleId === 4 ? '服务中心' : '个人'}}贡献度数据</h2>
      <div class="card-wrapper">
        <div class="card" v-for="(info, i) in infoCardData" :key="`info-${i}`">
          <info-card shadow :color="info.color" :icon="info.icon" :icon-size="36">
            <p class="count-style">{{info.count}}</p>
            <p>
              {{ info.title }}
              <Tooltip :content="info.tipText[String(roleId)]" placement="bottom" :max-width="200" transfer>
                <Icon type="md-help-circle" />
              </Tooltip>
            </p>
          </info-card>
        </div>
      </div>
      <Row v-if="roleId === 4" style="margin-top: 10px">
        <h2 style="margin-bottom: 20px">顾问贡献度数据明细</h2>
        <Table :columns="cols" :data="tableData"></Table>
      </Row>
    </div>
    <div v-else class="home-bg" @click="download"></div>
  </div>
</template>

<script>
import { setFrom } from '@/api/bd'
import InfoCard from '_c/info-card'
import { mapState } from 'vuex'
import { queryPersonalPerformance, queryServicePerformance } from '@/api/user'
import { dateFormat } from '@/utils/utils'
export default {
  name: 'home',
  components: {
    InfoCard
  },
  data () {
    return {
      infoCardData: [
        { title: '昨日缴费', icon: 'logo-yen', count: '--', color: '#2d8cf0', tipText: { '4': '全分校昨日缴费用户数', '5': '全分校昨日缴费用户数' } },
        { title: '昨日报名', icon: 'ios-create', count: '--', color: '#19be6b', tipText: { '4': '全分校上日缴费用户中，通过您所在服务中心顾问的盘古账号帮助家长进行报名的数量', '5': '全分校上日缴费用户中，通过您的盘古账号帮助家长进行报名的数量' } },
        { title: '昨日维护', icon: 'md-construct', count: '--', color: '#ff9900', tipText: { '4': '全分校上日缴费用户中，绑定在您所在服务中心顾问的盘古账号下的用户数', '5': '全分校上日缴费用户中，绑定在您的盘古账号下的用户数量' } },
        { title: '昨日注册', icon: 'md-person-add', count: '--', color: '#ed3f14', tipText: { '4': '全分校上日缴费用户中，通过您所在服务中心顾问的盘古账号完成注册的用户数量', '5': '全分校上日缴费用户中，通过您的盘古账号完成注册的用户数量' } }
      ],
      cols: [
        { title: '顾问', align: 'center', key: 'userName' },
        { title: '昨日报名量', align: 'center', key: 'registCount' },
        { title: '昨日维护量', align: 'center', key: 'defendCount' },
        { title: '昨日注册量', align: 'center', key: 'newCount' }
      ],
      tableData: []
    }
  },
  methods: {
    download () {
      window.open('https://funsion-video.oss-cn-beijing.aliyuncs.com/master/%E7%9B%98%E5%8F%A4%E5%B9%B3%E5%8F%B0%E4%BD%BF%E7%94%A8%E6%89%8B%E5%86%8C-3.0.pdf', 'DOWNLOAD')
    },
    queryData () {
      const yesterday = dateFormat(new Date(Date.now() - 24 * 60 * 60 * 1000), 'yyyy-MM-dd')
      if (this.roleId === 4) {
        queryServicePerformance({
          deptCode: this.baseDataId,
          localDate: yesterday
        }).then(data => {
          if (data) {
            this.infoCardData[0].count = data.payCount === null ? '--' : data.payCount
            this.infoCardData[1].count = data.registCount === null ? '--' : data.registCount
            this.infoCardData[2].count = data.defendCount === null ? '--' : data.defendCount
            this.infoCardData[3].count = data.newCount === null ? '--' : data.newCount
            this.tableData = data.performanceWorkList || []
          }
        }).catch(err => {
          this.$Message.error('查询服务中心绩效数据错误：' + err.message)
        })
      } else if (this.roleId === 5) {
        queryPersonalPerformance({
          localDate: yesterday
        }).then(data => {
          if (data) {
            this.infoCardData[0].count = data.payCount === null ? '--' : data.payCount
            this.infoCardData[1].count = data.registCount === null ? '--' : data.registCount
            this.infoCardData[2].count = data.defendCount === null ? '--' : data.defendCount
            this.infoCardData[3].count = data.newCount === null ? '--' : data.newCount
          }
        }).catch(err => {
          this.$Message.error('查询个人绩效数据错误：' + err.message)
        })
      } else {

      }
    }
  },
  computed: {
    ...mapState({
      roleId: state => state.roleRelativeData.userInfo.crmRoleId,
      baseDataId: state => state.roleRelativeData.userInfo.baseDataId
    })
  },
  watch: {
    roleId () {
      this.queryData()
    }
  },
  mounted () {
    setFrom({
      apiName: 'initPage'
    })
    this.$nextTick(() => {
      this.queryData()
    })
  }
}
</script>

<style lang="less" scoped >
  .wrapper {
    width: 100%;
    height: 100%;
    .card-wrapper {
      display: flex;
      flex-wrap: wrap;
      .card {
        width: 320px;
        height: 120px;
        padding-bottom: 10px;
        margin-right: 20px
      }
    }
    .home-bg {
      width: 100%;
      height: 100%;
      background: url('../../../assets/images/home.png') center center no-repeat ;
      background-size: contain;
      cursor: pointer;
    }
    .count-style{
      font-size: 50px;
    }
  }
  .bg-white {
    background: white;
  }
  .bg-other {
    background: #f5f7f9;
  }
</style>
